import React from 'react';
import { Tabs } from 'antd';
import Users from './Users';
const onChange = (key) => {
    console.log(key);
};
const items = [
    {
        key: '1',
        label: 'Tab 1',
        children: <Users />,
    },
    {
        key: '2',
        label: 'Tab 2',
        children: 'Content of Tab Pane 2',
    },
    {
        key: '3',
        label: 'Tab 3',
        children: 'Content of Tab Pane 3',
    },
];
const App = () => <Tabs defaultActiveKey="1" items={items} onChange={onChange} />;
export default App;